nset;-webkit-backdrop-filter:blur(15px);backdrop-filter:blur(15px)}#form-overlay.active #form{-webkit-transform:translateY(0);transform:translateY(0)}#form-overlay #form{position:absolute;width:75%;height:65%;top:0;left:0;right:0;bottom:0;margin:auto;padding:50px;display:flex;justify-content:center;flex-direction:column;-webkit-transform:translateY(20px);transform:translateY(20px);transition:ease-in-out 0.3s all}#form-overlay #form .close{position:absolute;top:50px;right:50px;font-size:40px;line-height:40px;cursor:pointer}#form-overlay #form .close:after{content:"\00D7"}#form-overlay #form .content{position:relative;text-align:center}#form-overlay #form .content .form-title{font-size:26px;font-family:"Source Sans Pro",sans-serif;font-weight:700;margin-bottom:20px}#form-overlay #form .content .form-input{background:unset;border-bottom:1px solid #EBEBEB;border-top:0;border-left:0;border-right:0;text-align:center}#form-overlay #form .content .form-input:focus{border-bottom:1px solid rgba(0,0,0,0);outline:none}#form-overlay #form .content .submit-button{color:#fff;font-size:16px;font-weight:bold;background-color:#E50915;border:0;border-radius:20px;cursor:pointer;padding:5px 25px;margin:30px auto 0;display:block;position:absolute;left:0;right:0;transition:ease-in-out 0.3s all}#form-overlay #form .content .submit-button:focus{outline:none}#form-overlay #form .content .submit-button:hover{background-color:#B70D11}#form-overlay #form .content.return-form .form-section{font-size:20px}#form-overlay #form .content.return-form .form-input{min-width:200px;height:40px;display:inline-block;font-size:inherit}#form-overlay #form .content.return-form .form-input.comment{min-width:300px}#form-overlay #form .content.person-form .form-input{width:60%;min-width:300px;height:70px;display:block;font-weight:500;font-family:"Lato",sans-serif;font-size:36px;margin:0 auto}.property-editor{position:relative}.property-editor div{transition:ease-in-out 0.3s all}.property-editor .content,.property